.account-modal {
  &-body {
    font-size: 14px;
    padding: 0 24px 24px;
    .desc {
      font-size: 18px;
      display: flex;
      justify-content: space-between;
      .chain {
        display: flex;
        justify-content: space-between;
        width: 48px;
        font-size: 14px;
        font-weight: 400;
        align-items: center;
        line-height: 22px;
      }
      margin: 12px 0;
    }
    .hash-add {
      font-size: 12px;
      font-weight: 400;
      display: flex;
      height: 40px;
      background: rgba(85, 119, 253, 0.1);
      border-radius: 4px;
      align-items: center;
      color: #93A1B8;
      padding: 0 16px;
      img {
        margin-right: 16px;
      }
    }
    .anchor {
      font-weight: 500;
      display: flex;
      margin: 16px 0;
      text-decoration-color: #93A1B8;
      color: #93A1B8; 
      div {
        color: #93A1B8;
        margin-right: 16px;
        display: flex;
        align-items: center;
        img {
          margin-left: 4px;
          cursor: pointer;
          color: #93A1B8;
        }
      }
      // div:hover {
      //   text-decoration: underline;      
      // }
    }
    .action {
      display: flex;
      justify-content: space-between;
    }
  }
}