@import './assets/css/variable.less';

html[lang=zh-TW],html[lang=zh-CN] {
    body {
        font-weight: normal;
    }
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    /* display: none; <- Crashes Chrome on hover */
    -webkit-appearance: none;
    margin: 0;
    /* <-- Apparently some margin are still there even though it's hidden */
}

::-webkit-scrollbar {
    width: 8px;
}

/* Track */
::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.1);
    border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #203B60;
    border-radius: 10px;
}

input[type=number] {
    -moz-appearance: textfield;
    /* Firefox */
}

body {
    background-color: @background_color;
    font-family: Barlow;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    color: @defaultFontColor;
}


@-webkit-keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes fadeOut {
    from {
        opacity: 1;
    }

    to {
        opacity: 0;
    }
}

@-webkit-keyframes fadeOut {
    from {
        opacity: 1;
    }

    to {
        opacity: 0;
    }
}

@-webkit-keyframes pulse {
    50% {
        opacity: .5
    }
}

@keyframes pulse {
    50% {
        opacity: .5
    }
}

.loading-line {
    width: 100px;
    height: 16px;
    border-radius: 4px;
    background-color: #203B60;
    animation: pulse 2s cubic-bezier(0.2, 0, 0.4, 1) infinite;
    display: inline-block;
}