.dialog {
  font-size: 18px;
  color: #E0ECFF;
  background: #0A172B;
  width: 375px;
  border-radius: 4px;
  &-header {
    font-weight: 600;
    font-size: 22px;
    height: 80px;
    display: flex;
    align-items: center;
    padding: 0 24px;
    justify-content: space-between;
    .close-wallet-modal {
      cursor: pointer;
    }
  }
  &-footer {
    color: #93A1B8;
    font-size: 12px;
    padding: 0 24px 24px;
    a {
      text-decoration: underline;
    }
  }
}