
.trade-hint{
  padding-left: 24px;
  color: rgb(102, 60, 0);
  display: flex;
  align-items: center;
  font-weight: 400;
  padding: 6px 16px;
  background-color: rgb(255, 244, 229);
  min-width: 375px;
  a{
    color: rgb(102, 60, 0);
    text-decoration: underline;
  }
  justify-content: space-between;
  .left-hint{
    display: flex;
    align-items: center;
    padding: 8px 0;
  }
  .close-hint{
    cursor: pointer;
  }
  .MuiSvgIcon-fontSizeInherit {
    font-size: inherit;
}
.MuiSvgIcon-root {
    fill: currentColor;
    width: 1em;
    height: 1em;
    display: inline-block;
    margin-right: 10px;
    font-size: 1.5rem;
    transition: fill 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    flex-shrink: 0;
    user-select: none;
    color: #ff9800;
}
}