@-moz-keyframes fadein {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0px);
  }
}
@-webkit-keyframes fadein {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0px);
  }
}
@keyframes fadein {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0px);
  }
}
.menu {
  .menu-item {
    margin-top: 24px;
    cursor: pointer;
    white-space: nowrap;
    a {
      color : #93A1B8;
      text-decoration: none;
    }
  }
  .trade-to-earn-item,.docs-item,.more-item{
    display: flex;
    align-items: center;
    .sub-item{
      display: flex;
      align-items: center;
    }
  }
  .docs-item,.more-item{
    img{
      margin-top: 5px;
    }
  }

  .trade-to-earn-item,.practice-item{
    display: flex;
    width: 130px !important;
    justify-content: space-between;
    a{
      margin-left: 5px;
    }
  }
  .menu-item.selected  a{
    color : #E0ECFF;
    cursor: pointer;
    background: #5577FD1A;
  }
  .sub-menu-item {
    padding: 16px;
    text-align: left;
    border : 1px solid transparent;
    height: 48px;
  }
  .sub-menu-item:hover {
    // border: 1px solid #CD7A37;
    border-radius: 4px;
    background: #5577FD1A;
  }
  .sub-menu-item:last-child{
    margin-bottom: 24px;
  }

  .__react_component_tooltip.show {
    opacity: 1;
    width : 216px;
    padding : 0;
  }
  .__react_component_tooltip.show::after{
    content: none;
  }
  .__react_component_tooltip.show.place-right{
    margin: 0px;
  }

  .footer {
    display: none;
  }
}
@media only screen and (max-width : 375px){

  .show-side-menu {
    .menu {
      opacity: 1;
      width: 100%;
      display: flex;
      z-index: 2;
      animation: fadein .3s .0s linear;
      transform: translateX(0px);
    }
    .footer {
      display: block;
      bottom: 20px;
      position: fixed;
    }
  }
}
@media only screen and (max-width : 760px) {
  .menu {
    background: #0A172B;
    position: fixed;
    flex-direction: column;
    top: 0;
    left: 0;
    padding: 24px 32px;
    width: 100%;
    height: 100%;
    z-index: 2;
    display: flex;
    flex-direction: column;
    background: #0A172B;
    .menu-item {
      .beta-icon {
        width: 42px;
      }
    }
    .back-icon {
      display: block;
    }
    .footer {
      display: none;
    }
  }
  .show-side-menu {
    .menu {
      opacity: 1;
      width: 100%;
      display: flex;
      z-index: 2;
      animation: fadein .3s .0s linear;
      transform: translateX(0px);
    }
    .footer {
      display: block;
      bottom: 20px;
      position: fixed;
    }
  }
}
