@media only screen and (min-width : 1141px){
  .menu {
    display: flex;
    justify-content: space-around;
    margin-left: 14px;
    font-size: 16px;
    background: none;
    position: initial;
    flex-direction: row;
    padding: 0px;
    
    .back-icon {
      display: none;
    }
    .menu-item.long {
      width :128px
    }
    .menu-item {
      position : relative;
      width: 96px;
      margin: auto;
      display: flex;
      justify-content: center;

      .beta-icon {
        position : absolute;
        top: -14px;
        left: 28px;
      }
    }
    .menu-item:hover > a,.menu-item:hover >.sub-menu,.menu-item.selected > a,.sub-menu-item:hover > a{
      color : #E0ECFF;
      cursor: pointer;
    }
    .__react_component_tooltip.show.place-bottom{
      margin-top: 26px;
      margin-left: 30px;
    }
    .footer {
      display: none;
    }
  }
}
