.wallet-modal {
  &-body {
    margin: 0 24px 24px;
    &-item {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 32px 32px 32px 16px;
      border: 1px solid #203B60;
      border-bottom: 1px solid transparent;
      .name {
        display: flex;
        align-items: center;
        .spinner-border {
          margin-left: 8px;
        }
      }
      .status {
        display: flex;
        align-items: center;
        width: 16px;
        .connected {
          border: 2px solid #CD7A37;
          width: 6px;
          height: 6px;
          border-radius: 50%;
          background-color: #CD7A37;
        }
      }
    }
    &-item:hover {
      cursor: pointer;
      border : 1px solid #CD7A37!important;
      background: url(../../assets/img/wallet-hover-bg.svg) no-repeat;
    }
    &-item:last-child {
      border-bottom: 1px solid #203B60;
    }
  }
}