@import '../../assets/css/variable.less';

.adas {
  all: unset;
  font-family: Helvetica, Arial, sans-serif;
  display: inline-block;
  max-width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  background-color: #0445AF;
  color: #FFFFFF;
  font-size: 20px;
  border-radius: 25px;
  padding: 0 33px;
  font-weight: bold;
  height: 50px;
  cursor: pointer;
  line-height: 50px;
  text-align: center;
  margin: 0;
  text-decoration: none;

}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 72px;

  .menu {
    display: flex;
    transform: inherit;
  }

  .left {
    display: flex;
    align-items: center;

    .logo {
      margin-left: 48px;
      display: flex;
      align-items: center;

      >a {
        color: @whiteFontColor;
        text-decoration: none;
        display: flex;
      }

      .name {
        margin: auto 12px;
        color: @whiteFontColor;
        font-size: @20FontSize;
      }
    }
  }

  .right {
    display: flex;
    margin-right: 24px;
  }

  .small-menu {
    margin-left: 24px;
  }

}
@media only screen and (min-width: 0px) {
  .header {
    .right {
      margin-right: 0px;
    }
  }
}

@media only screen and (min-width: 1140px) {
  .header {
    .right {
      margin-right: 24px;
    }
  }
}

