@-moz-keyframes fadeout {
  0% {
    transform: translateX(0px);
  }

  100% {
    transform: translateX(-100%);
  }
}

@-webkit-keyframes fadeout {
  0% {
    transform: translateX(0px);
  }

  100% {
    transform: translateX(-100%);
  }
}

@keyframes fadeout {
  0% {
    transform: translateX(0px);
  }

  100% {
    transform: translateX(-100%);
  }
}

//小于760 大于375px
@media only screen and (max-width : 375px) {
  .header {
    .left .logo {
      margin-left: 32px;
    }

    .menu {
      display: flex;
      transform: translateX(-100%);
      -moz-animation: fadeout 0.2s 0s linear;
      -webkit-animation: fadeout 0.2s 0s linear;
      animation: fadeout 0.2s 0s linear;
    }

    .small-menu {
      display: block;
    }

    // .lang-select,
    .network-select {
      display: none;
    }
  }
}

@media only screen and (max-width : 760px) {

  .header {
    .left .logo {
      margin-left: 32px;
    }

    .menu {
      display: flex;
      transform: translateX(-100%);
      -moz-animation: fadeout 0.2s 0s linear;
      -webkit-animation: fadeout 0.2s 0s linear;
      animation: fadeout 0.2s 0s linear;
    }

    .small-menu {
      display: block;
    }

    // .lang-select,
    .network-select {
      display: none;
    }
  }
}

@media only screen and (max-width : 1140px) {

  .header {
    .left .logo {
      margin-left: 32px;
    }

    .menu {
      display: flex;
      transform: translateX(-100%);
      -moz-animation: fadeout 0.2s 0s linear;
      -webkit-animation: fadeout 0.2s 0s linear;
      animation: fadeout 0.2s 0s linear;
    }

    .small-menu {
      display: block;
    }

    // .lang-select,
    .network-select {
      display: none;
    }
    .lang-select{
      margin-right: 8px;
    }
  }
}

@media only screen and (min-width : 1141px) {
  .header {
    .menu {
      display: flex;
      transform: inherit;
    }

    .small-menu {
      display: none;
    }

    .lang-select,
    .network-select {
      display: flex;
    }
  }
}

